h2 {
    font-size: 1.8em;
    line-height: 1.1em;
    font-weight: 800;
    border-bottom: solid 5px $baseColor;
    margin: .5em 0;
}

h3 {
    border-left: solid $baseColor 10px;
    font-size: 1.5em;
    line-height: 1.3em;
    padding-left: .3em;
    margin: .5em 0;
    font-weight: 600;
}

h4 {
    font-size: 1.3em;
    line-height: 1.3em;
    color: $baseColor;
    margin: .3em 0;
    transition: .5s;
    font-weight: 600;
}

a {
    text-decoration: none;
    color: $baseColor;
    padding: 0 .3em;
    transition: .5s;
    border-radius: 3px;
    &:hover {
        text-decoration: none;
        background: $baseColor;
        color: $whiteColor;
    }
}

ul.check {
    list-style: none;
}

ul.check li::before {
    font-family: FontAwesome;
    font-weight: 800;
    content: '\f00c';
}