#header {
    text-align: center;
    margin: auto;
    position: sticky;
    top: 0;
    #headerContent {
        background-color: rgb(0, 0, 0);
        margin: auto;
        width: 80%;
        border-radius: 0px 0px 1em 1em;
        border: white solid .5em;
        border-top: none;
        a {
            &:hover {
                background: none;
            }
            img {
                width: calc(100% - 1em);
                max-width: 300px;

            }
        }
    }
}