@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@100;400;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@400;800&display=swap);
body{margin:0;font-size:16px;word-break:break-all;font-family:'M PLUS Rounded 1c', "BIZ UDPGothic", YakuHanJPs, Lato, "Noto Sans JP", "ヒラギノ角ゴ ProN W3", Meiryo, sans-serif;scroll-behavior:smooth;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#000;color:#fff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#header{text-align:center;margin:auto;position:-webkit-sticky;position:sticky;top:0}#header #headerContent{background-color:#000;margin:auto;width:80%;border-radius:0px 0px 1em 1em;border:white solid .5em;border-top:none}#header #headerContent a:hover{background:none}#header #headerContent a img{width:calc(100% - 1em);max-width:300px}h2{font-size:1.8em;line-height:1.1em;font-weight:800;border-bottom:solid 5px #03A9F4;margin:.5em 0}h3{border-left:solid #03A9F4 10px;font-size:1.5em;line-height:1.3em;padding-left:.3em;margin:.5em 0;font-weight:600}h4{font-size:1.3em;line-height:1.3em;color:#03A9F4;margin:.3em 0;transition:.5s;font-weight:600}a{text-decoration:none;color:#03A9F4;padding:0 .3em;transition:.5s;border-radius:3px}a:hover{text-decoration:none;background:#03A9F4;color:#fff}ul.check{list-style:none}ul.check li::before{font-family:FontAwesome;font-weight:800;content:'\f00c'}

